/*Gray and brand colors*/
/* roboto-300 - cyrillic_latin */
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 300;
  font-display: swap;
  src: local("Roboto Light"), local("Roboto-Light"), url("../fonts/roboto-v20-cyrillic_latin-300.woff2") format("woff2"), url("../fonts/roboto-v20-cyrillic_latin-300.woff") format("woff"); }

/* roboto-400 - cyrillic_latin */
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Roboto"), local("Roboto-Regular"), url("../fonts/roboto-v20-cyrillic_latin-regular.woff2") format("woff2"), url("../fonts/roboto-v20-cyrillic_latin-regular.woff") format("woff"); }

/* roboto-500 - cyrillic_latin */
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: local("Roboto Medium"), local("Roboto-Medium"), url("../fonts/roboto-v20-cyrillic_latin-500.woff2") format("woff2"), url("../fonts/roboto-v20-cyrillic_latin-500.woff") format("woff"); }

html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  position: relative;
  min-width: 320px;
  margin: 0;
  font-family: "Roboto", "Arial", sans-serif;
  font-weight: 400; }

img {
  max-width: 100%;
  height: auto; }

video {
  width: 100%;
  height: auto; }

h1,
h2,
h3,
h4,
p {
  margin: 0;
  word-wrap: break-word; }

h2 {
  font-size: 5em;
  font-weight: 400; }

a {
  text-decoration: none;
  cursor: pointer; }

fieldset {
  margin: 0;
  padding: 0; }

blockquote {
  margin: 0; }

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

/*анимация прозрачости для .homepage*/
.anim-opacity {
  animation: anim-opacity 0.2s linear 3s both; }

/**
 * ----------------------------------------
 * animation anim-opacity
 * ----------------------------------------
 */
@keyframes anim-opacity {
  0% {
    opacity: 1;
    transition: visibility; }
  100% {
    opacity: 0;
    visibility: hidden;
    transition: visibility; } }

.contact {
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50vw;
  height: 100vh;
  border-right: 1px solid #e1e1e1;
  background: #2c2c2c;
  cursor: pointer; }
  .contact:hover {
    background-color: #e1e1e1; }
  .contact:hover .contact__link {
    color: #3d3d3d; }

.contact__link {
  color: #e1e1e1; }

.homepage {
  position: fixed;
  z-index: 100;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  background: #ffffff; }

.homepage__wrap {
  display: block;
  width: 75%; }

.me {
  position: fixed;
  top: 0;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50vw;
  height: 100vh;
  background: #2c2c2c;
  cursor: pointer; }
  .me:hover {
    background-color: #e1e1e1; }
  .me:hover .me__link {
    color: #3d3d3d; }

.me__link {
  color: #e1e1e1; }

.page-footer {
  margin: 0 auto; }

.page-header {
  margin: 0 auto; }

/*анимация движения текста по оси Z*/
.slide-out-fwd-center {
  animation: slide-out-fwd-center 1s cubic-bezier(0.55, 0.085, 0.68, 0.53) 2.5s both; }

/**
 * ----------------------------------------
 * animation slide-out-fwd-center
 * ----------------------------------------
 */
@keyframes slide-out-fwd-center {
  0% {
    transform: translateZ(1); }
  100% {
    transform: perspective(100px) translateZ(600px); } }
